/* Imports */
import { Component } from "react";

/* Social network links */
let instagramLink = "https://www.instagram.com/leandroblandi/";
let githubLink = "https://github.com/leandroblandi";
let stackLink = "https://stackoverflow.com/users/14716565/leandro-blandi";


/* Contact Component */
export default class Contact extends Component {
    render() {
        return (
            <div className="contact-container" id="contact">
                <div className="title-contact">
                    <h2>Contact me</h2>
                </div>
                <div className="social-media-contact">
                    <ul>
                        <li>
                            <i className="fab fa-instagram" aria-hidden="true"></i>
                            <a href={ instagramLink }>Follow me on Instagram</a>
                        </li>
                        <li>
                            <i className="fab fa-github" aria-hidden="true"></i>
                            <a href={ githubLink }>Check my repositories</a>
                        </li>
                        <li>
                            <i className="fab fa-stack-overflow" aria-hidden="true"></i>
                            <a href={ stackLink }>See my Stackoverflow account</a>
                        </li>
                    </ul>
                </div>
            </div>
        );
    }
}