/* Imports */
import { Component } from "react";

/* Skills phrase */
let phrase = "As a beginner programmer, I don't have so many projects. But as you can see, I have some goods skills at web development. Also I have some good projects at Github. Go to check it!";


/* Skills Component */
/* IMPORTANT, Fix extense Component, modularize it */
export default class Skills extends Component {
    render() {
        return (
            <div className="skills-container"  id="skills">
                <div className="skills-title">
                    <h2 className="skills-title-content">My Skills</h2>
                </div>
                <div className="skills-content">
                    <div className="mini-description">
                        <p>{phrase}</p>
                    </div>
                    <div className="skills-info">
                        <div className="each-skill">
                            <div className="skill-name">
                                <i className="fab fa-html5 hticon"></i>
                                <p>HTML</p>
                            </div>
                            <div className="progressbar">
                                <div className="html">80%</div>
                            </div>      
                        </div>
                        <div className="each-skill">
                            <div className="skill-name">
                                <i className="fab fa-css3-alt cssicon"></i>
                                <p>CSS</p>
                            </div>
                            <div className="progressbar">
                                <div className="css">75%</div>
                            </div>
                        </div>
                        <div className="each-skill">
                            <div className="skill-name">
                                <i className="fab fa-js jsicon"></i>
                                <p>Javascript</p>
                            </div>
                            <div className="progressbar">
                                <div className="js">80%</div>
                            </div>
                        </div>
                        <div className="each-skill">
                            <div className="skill-name">
                                <i className="fab fa-python pyicon"></i>
                                <p>Python</p>
                            </div>
                            <div className="progressbar">
                                <div className="py">85%</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}