
/* Components */
import { Component } from "react";
import ImageGallery from 'react-image-gallery';

/* Images */
import projectImage1 from "../assets/projects/facebook-post.png";
import projectImage2 from "../assets/projects/counter.png";
import projectImage4 from "../assets/projects/pdfconverter.png";
import projectImage5 from "../assets/projects/calculadora.png";

const images = [
    {
        "original": projectImage1,
        "thumbnail": projectImage1,
        "description": "Facebook post with React"
    },
    {
        "original": projectImage2,
        "thumbnail": projectImage2,
        "description": "Counter App with React"
    },
    {
        "original": projectImage4,
        "thumbnail": projectImage4,
        "description": "PDF Converter with Python"
    },
    {
        "original": projectImage5,
        "thumbnail": projectImage5,
        "description": "Basic calculator with Python"
    }
];

export default class Project extends Component {
    render() {
        return (
            <div className="projects-container" id="box">
                <div className="project-title">
                    <h2>My Projects</h2>
                </div>
{/*                 <div className="project-information">
                    <p>{ projectInfo }</p>
                </div> */}
                <div className="project-image gallery">
                    {/* <img src={ projectImage } alt="ByG Riegos Webpage"/> */}
                    <ImageGallery 
                        items={images}
                        autoPlay={true}
                        showThumbnails={false} 
                        showPlayButton={false}
                        showFullscreenButton={true}
                        showNav={true} />
                </div>
            </div>
        );
    } 
}