/* Imports */
import { Component } from "react";
import $ from "jquery";

/* Section pages links */
let homeLink = "https://leandroblandi.github.io/portfolio-react/";
let contactLink = "https://leandroblandi.github.io/portfolio-react/#contact";


/* Arrow func for open and close responsive menu */
const handleClickMenu = (e) => {
    if($(".icon-container i").hasClass("fas fa-bars")) {
        $(".menu-container").toggle(150, "swing");
        $(".icon-container i")
            .removeClass("fas fa-bars")
            .addClass("fas fa-times");
        e.preventDefault();
    }
    else {
        $(".menu-container").toggle(150, "swing");
        $(".icon-container i")
            .removeClass("fas fa-times")
            .addClass("fas fa-bars");
        e.preventDefault();
    }
    
}


/* Header component */
export default class Header extends Component { 
    render() {
        return (
            <div className="header-container">
                <div className="icon-container">
                    <i data-transition="fade" className="fas fa-bars menu-icon" onClick={handleClickMenu}></i>
                </div>
                <div className="menu-container">
                    <ul className="list">
                        <li className="list-element">
                            <a href={ homeLink }>
                                <i className="fas fa-home icon-button"></i>
                                Home
                            </a>
                        </li>
                        <li className="list-element">
                            <a href={ contactLink }>
                                <i className="fas fa-user-alt icon-button"></i>
                                Contact me
                            </a>
                        </li>
                    </ul>
                </div>
            </div>
        );
    }
}