
/* ReactDOM.render method import, and Jquery */
import { render } from "react-dom";  
import $ from "jquery";

/* Main Component Import */
import Main from "./Components/Main";

const root = document.getElementById("root-container");

/**
 * Despues de 1.95 segundos
 * se esconde el div.pre-loader,
 * y despues de 1 mili-segundo
 * se hace render del componente <Main />
 */
setTimeout(()=>{
    $('.pre-loader').hide('fast');
    setTimeout(()=>{
        render(<Main />, root);
    }, 100);
}, 1950);

