/* Imports */
import { Component } from "react";

/* Name and dynamic date */
const footerName = "Leandro Blandi";
const footerYear = new Date().getFullYear();
 
/* Footer Component */
export default class Footer extends Component {
    render() {
        return (
            <div className="footer-container">
                <div className="social-media-icons">
                    <a className="link" href="https://www.instagram.com/leandroblandi/">
                        <i className="fab fa-instagram" aria-hidden="true"></i>
                    </a>
                    <a className="link" href="https://github.com/leandroblandi">
                        <i className="fab fa-github" aria-hidden="true"></i>
                    </a>
                    <a className="link" href="https://stackoverflow.com/users/14716565/leandro-blandi">
                        <i className="fab fa-stack-overflow" aria-hidden="true"></i>
                    </a>
                </div>
                <div className="developed-with">
                    <div className="centered-box">
                        <p>Developed with <span className="react">React</span></p><i className="fab fa-react"></i>
                    </div>
                </div>
                <div className="about-this">
                    <p className="parraph">
                        <b>{footerName}</b> {footerYear}
                    </p>
                </div>
            </div>
        );
    }
 }
 